.slick-slider
	position: relative
	display: block
	box-sizing: border-box
	-moz-box-sizing: border-box
	-webkit-touch-callout: none
	-webkit-user-select: none
	-khtml-user-select: none
	-moz-user-select: none
	-ms-user-select: none
	user-select: none
	-ms-touch-action: pan-y
	touch-action: pan-y
	-webkit-tap-highlight-color: transparent
.slick-list
	position: relative
	overflow: hidden
	display: block
	margin: 0
	padding: 0
	&:focus
		outline: none
	.slick-loading &
		background: white slick-image-url("ajax-loader.gif") center center no-repeat
	&.dragging
		cursor: pointer
		cursor: hand

.slick-slider .slick-list,
.slick-track,
.slick-slide,
.slick-slide img
	-webkit-transform: translate3d(0, 0, 0)
	-moz-transform: translate3d(0, 0, 0)
	-ms-transform: translate3d(0, 0, 0)
	-o-transform: translate3d(0, 0, 0)
	transform: translate3d(0, 0, 0)

.slick-track
	position: relative
	left: 0
	top: 0
	display: block
	zoom: 1
	&:before,
	&:after
		content: ""
		display: table
	&:after
		clear: both
	.slick-loading &
		visibility: hidden

.slick-slide
	float: left
	height: 100%
	min-height: 1px
	img
		display: block
		max-width: 100%
	&.slick-loading img
		display: none
	display: none
	&.dragging img
		pointer-events: none
	.slick-initialized &
		display: block
	.slick-loading &
		visibility: hidden
	.slick-vertical &
		display: block
		height: auto
		border: 1px solid transparent

.slick-prev,
.slick-next
	position: absolute
	bottom: -100px
	+hide-text
	background: none
	opacity: 0.36
	transition: opacity 200ms
	&:hover
		opacity: 1 
	&:focus
		outline: none
	&.slick-disabled
		opacity: 0.2
.slick-prev
	left: 0
	+s(prev)
.slick-next
	left: 100px
	+s(next)
.slick-dots
	text-align: center
	position: absolute
	margin: 0
	top: 50%
	right: 40px
	transform: translate3d(0, -50%, 0)
	li
		position: relative
		padding: 7px 0
		&.slick-active
			button
				background: #bf2931 
		&:before
			display: none

	button
		display: block
		width: 14px
		height: 14px
		background: #1e1e1e
		cursor: pointer
		+hide-text
		border-radius: 50%
		&:hover
			box-shadow: none
