.slick-prev, .slick-next { position: absolute; bottom: -100px; font: 0/0 a; text-shadow: none; color: transparent; background: none; opacity: 0.36; transition: opacity 200ms; }
.slick-prev:hover, .slick-next:hover { opacity: 1; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }

.slick-prev { left: 0; background-image: url("../img/icons.png"); width: 62px; height: 61px; background-position: -66px 0px; }

